.search-form input[type="text"] {
  border: 2px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
}

.search-form button[type="submit"] {
  color: #fff;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
}

.gallery {
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
  display: grid;
}

.photo-card {
  border: 1px solid #ccc;
  border-radius: 5px;
  overflow: hidden;
}

.photo-card img {
  max-width: 100%;
  height: auto;
}

.photo-card .info {
  padding: 10px;
}

.notiflix-failure {
  color: #fff;
  background-color: red;
}

.load-more {
  color: #fff;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
}

